        BDJOptions: ['ÊòØÂê¶ÁªÉ‰π†ÂÖ´ÊÆµÈî¶'],
        BDJTime: 0,
        isBDJTimeShow: false,

        isbplShow: false,
        isyljShow: false,
        isnqlShow: false,
        isrnShow: false,
        isBDJShow: false,
      }
    },
    watch: {
      isWalked (val) {
        this.isWalkTimeShow = !!val[1];
      },
      walkTotal (val) {
        if (isNaN(Number(val))) {
          Toast('ËØ∑ËæìÂÖ•Á∫ØÊï∞Â≠ó!');
          this.walkTotalState = 'error';
        }
        else {
          this.walkTotalState = '';
        }
      },

      //stage 5
      isBDJ (val) {
        this.isBDJTimeShow = !!val[1];
      },
    },
    methods: {
      // general
      getStage () {
          this.$http.get(`https://api.cancercare.net.cn/api/questionnaire?openid=${this.openid}`)
          .then(res => {
            console.log(res)
            this.stage = res.body.content.stage;
          }, res => {
            Toast('ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï!');
          }) 
      }
      // stage 5
    },
    mounted () {
      this.openid = getQueryString('openid')
      this.getStage()
    }
  }

</script>
